<template>
  <div>
    <el-row>
      <el-button class="fl" type="primary" @click="handleAdd">新增看板</el-button>
      <el-button class="fl" type="primary" @click="handleSet">看板设置</el-button>
    </el-row>
    <div v-if="region1.length" class="box">
      <div class="title">
        <span>科技园</span>
      </div>
      <div class="cards">
        <el-carousel :interval="4000" type="card" height="250px" :autoplay="false" trigger="click">
          <el-carousel-item v-for="(c, i) in region1" :key="i">
            <div class="kanban">
              <div class="pic" />
              <el-descriptions class="des" :column="1">
                <el-descriptions-item label="创建时间">{{ c.createDate }}</el-descriptions-item>
                <el-descriptions-item label="最后修改时间">{{ c.updateDate }}</el-descriptions-item>
                <el-descriptions-item label="创建人">{{ c.createUser }}</el-descriptions-item>
              </el-descriptions>
            </div>
            <div class="kanban-bottom">
              <span class="name">{{ c.name }}</span>
              <span class="btns">
                <el-button type="primary" @click="handleEdit(c)">编辑</el-button>
                <el-button type="danger" @click="handleDelete(c)">删除</el-button>
              </span>
            </div>
          </el-carousel-item>
        </el-carousel>
      </div>
    </div>
    <div v-if="region2.length" class="box">
      <div class="title">
        <span>中院</span>
      </div>
      <div class="cards">
        <el-carousel :interval="4000" type="card" height="250px" :autoplay="false" trigger="click">
          <el-carousel-item v-for="(r, s) in region2" :key="s">
            <div class="kanban">
              <div class="pic" />
              <el-descriptions class="des" :column="1">
                <el-descriptions-item label="创建时间">{{ r.createDate }}</el-descriptions-item>
                <el-descriptions-item label="最后修改时间">{{ r.updateDate }}</el-descriptions-item>
                <el-descriptions-item label="创建人">{{ r.createUser }}</el-descriptions-item>
              </el-descriptions>
            </div>
            <div class="kanban-bottom">
              <span class="name">{{ r.name }}</span>
              <span class="btns">
                <el-button type="primary" @click="handleEdit(r)">编辑</el-button>
                <el-button type="danger" @click="handleDelete(r)">删除</el-button>
              </span>
            </div>
          </el-carousel-item>
        </el-carousel>
      </div>
    </div>
  </div>
</template>

<script>
import { getBoardListApi, deleteBoardApi } from '@/apis/modules/analysis/board'

export default {
  data() {
    return {
      region1: [],
      region2: []
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      const params = {}
      return new Promise((resolve, reject) => {
        getBoardListApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.region1 = data.filter(item => item.region === 1)
              this.region2 = data.filter(item => item.region === 2)
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    handleAdd() {
      this.$router.push({
        name: 'AddKanban'
      })
    },
    handleEdit(item) {
      this.$router.push({
        name: 'EditKanban',
        params: { id: item.id }
      })
    },
    handleDelete(item) {
      this.$confirm('此操作将永久删除该看板, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        deleteBoardApi({
          id: item.id
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: '删除成功'
            })
            this.init()
          }
        })
      }).catch(() => {
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    handleSet() {
      this.$router.push({
        name: 'SetKanban'
      })
    }
  }
}
</script>

<style scoped lang="scss">
.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
  background-color: #d3dce6;
}
.box {
  display: flex;
  .title {
    font-size: 14px;
    width: 100px;
  }
  .cards {
    width: calc(100% - 100px);
  }
}
.kanban {
  height: calc(100% - 40px);
  display: flex;
  justify-content: space-between;
  .pic {
    width: 55%;
    height: 100%;
    background: url('~@/assets/images/Snipaste_2023-11-29_09-16-41.png') no-repeat center;
    background-size: 100% 100%;
    border: 1px solid #C2C2C2;
  }
  .des {
    width: 40%;
    height: 100%;
    background-color: #FFFFFF;
    border: 1px solid #C2C2C2;
  }
}
.kanban-bottom {
  width: 100%;
  display: inline-flex;
  justify-content: space-around;
  align-items: center;
  height: 40px;
  .name {
    font-size: 18px;
    font-weight: 600;
  }
  .btns {

  }
}
</style>

